import React from "react";

export default function Command({ lang, command }) {
  return (
    <>
      <span className="prompt">{command.input[lang]}</span>
      <ul>
        {command.output.map((item) => {
          if (item instanceof Object) {
            return <li key={item[lang]}>{item[lang]}</li>;
          } else if (item instanceof String) {
            return <li key={item}>{item}</li>;
          } else {
            return null;
          }
        })}
      </ul>
    </>
  );
}
