import React from "react";

export default function LangButton({ lang, setLang }) {
  function changeLanguage() {
    setLang(lang === "en" ? "ru" : "en");
  }

  return (
    <span className="lang-btn" onClick={changeLanguage}>
      <span className={lang === "en" ? "selected-lang" : ""}>EN </span>
      <span className={lang === "ru" ? "selected-lang" : ""}>RU</span>
    </span>
  );
}
